export default {
	data() {
		return {
			
		};
	},
	methods:{
		formSubmit: function(e) {
	        var email = e.detail.value.email;
			var id = 16;
			var serverUrl = "http://tp5.1.com/api/user/updateemail";
			uni.request({
				url:serverUrl,
				method:"POST",
				data:{
					email:email,
					id:id
				},
				success:(res) => {
					console.log(res.data);
					if(res.data.code == 200){
						console.log("成功");
					}
				}
			});
		},
		// 使用uni.navigateBack可以返回到原页面 （返回登录页面)
		backOne:function(){
			uni.navigateBack({
				delta:1
			});
		}
	}
}
